@import "../../settings";
@import "../../mixins";

.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0,0,0, 0.5);
  z-index: 1;
  color: #000000;
}

.popupInner {
  position: absolute;
  left: 5%;
  right: 5%;
  top: 25%;
  padding: 19px;
  background: white;
  margin: 0 auto;
}

.popupButtonWrapper {
  text-align: center;
  button{
    width: auto;
    padding: 0.6rem 7.5rem;
    height: auto;
  }
}

.uniqueCodePopUp {
  text-align: center;
  font-weight: bold;
}

.codeTransform {
  text-transform: uppercase;
}

@include breakpoint(tablet) {
  .popupInner {
    left: 25%;
    right: 25%;
  }
}



