/*********************
BREAKPOINTS
*********************/
.footerLinkWrapper {
  font-size: 12px;
  margin: 4rem 0 25px 0; }
  .footerLinkWrapper ul {
    padding: 0;
    text-align: center;
    display: block; }
    .footerLinkWrapper ul li {
      display: inline-block;
      margin: 0 2rem; }

.socialLinks {
  text-align: center; }

.disclaimerText {
  margin-top: 25px;
  text-align: center;
  font-size: 16px; }

ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  list-style-type: none;
  font-size: 2em;
  margin: 0 auto; }

li {
  -ms-flex-positive: 1;
  flex-grow: 1; }

a {
  color: #fff;
  text-decoration: none;
  -webkit-transition: all .5s ease-in-out;
  -o-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out; }

[title="Twitter"]:hover {
  color: #55acee; }

[title="Instagram"]:hover {
  color: #e4405f; }

[title="Envelope"]:hover {
  color: #0077b5; }

[title="Facebook"]:hover {
  color: #3b5999; }

[title="Telegram"]:hover {
  color: #e6b60a; }

[center] {
  text-align: center; }

#footer_text {
  font-size: 1.5rem;
  text-align: center;
  max-width: 69rem;
  padding: 0 1rem;
  margin: 3rem auto 0; }

@media (min-width: 50em) {
  ul {
    width: 60%; } }

@media (min-width: 64em) {
  ul {
    width: 40%; } }

@media only screen and (max-width: 430px) {
  .footerLinkWrapper {
    margin: 0 auto 9rem; } }



